import { Visibility, VisibilityOff } from "@mui/icons-material";
import {
  Avatar,
  Box,
  Button,
  Checkbox,
  FormControl,
  FormControlLabel,
  Grid,
  IconButton,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Paper,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import ModeNightIcon from "@mui/icons-material/ModeNight";

import React from "react";
import { useFormik } from "formik";
import * as Yup from "yup";

import { Link } from "react-router-dom";
import "./SignUp.css";

const SignUp = ({ mode, setMode }) => {
  // formik
  const formik = useFormik({
    initialValues: {
      firstname: "",
      email: "",
      password: "",
      confirmpassword: "",
      number: "",
    },
    // یه تابعه
    onSubmit: (values) => {
      alert(JSON.stringify(values));
    },
    validationSchema: Yup.object({
      firstname: Yup.string()
        .max(10, "must be 10 characters or less")
        .min(5, "must be 5 characters or more")
        .required("*Required"),
      email: Yup.string().email("invalid email").required("*Required"),
      number: Yup.string().required("*Required"),
      password: Yup.string()
        .max(7, "must be 7 characters or less")
        .min(5, "must be 5 characters or more")
        .required("*Required")
        .matches(/^(?=.*[a-z])/, "lowercase a-z")
        .matches(/^(?=.*[A-Z])/, "upercase A-Z")
        .matches(/^(?=.*[1-9])/, "number 1-9")
        .matches(/^(?=.*[!,@,#,$,%,^,&,*,+,-])/, "characters(@,#,$...)"),
      // .matches(/^(?=.*[!,@,#,$,%,^,&,*])(?=.*[a-z])(?=.*[A-Z]).(?=.*[1-9])/, 'a-z, 1-9, characters'),

      confirmpassword: Yup.string()
        .required("*Required")
        .oneOf([Yup.ref("password"), null], "Passwords must match"),
    }),
  });

  // btn styles
  const btnstyle = {
    margin: "8px 0px",
  };
  // show & hide password
  const [showPassword, setShowPassword] = React.useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  // formstyle
  const formStyle = {
    padding: "30px 20px",
    width: 300,
    height: "auto",
    margin: "0px auto",
    zIndex: "1",
    position: "relative",
  };
  // paper styles
  const paperStyle = {
    height: "100vh",
  };
  // linkstyle
  const linkstyle = {
    color: "#1de9b6",
  };

  const headerStyle = { margin: "10px" };

  return (
    <Grid>
      <Box align="center">
        <ModeNightIcon />

        <Switch
          onChange={(e) => setMode(mode === "light" ? "dark" : "light")}
        />
      </Box>

      <form onSubmit={formik.handleSubmit}>
        <Paper style={paperStyle}>
          <Paper elevation={20} style={formStyle}>
            {/* header of form */}
            <Grid align="center">
              <Avatar src="data:image/png;base64,"></Avatar>
              <h2 style={headerStyle}>Sign UP</h2>
              <Typography variant="caption">
                Please fill this form to create an account!
              </Typography>
            </Grid>
            {/* end of header of form */}

            {/* name */}
            <TextField
              id="firstname"
              {...formik.getFieldProps("firstname")}
              sx={{ marginTop: "15px" }}
              label="Name"
              placeholder="Enter name"
              fullWidth
            />
            {formik.touched.firstname && formik.errors.firstname ? (
              <p style={{ color: "red", fontSize: "12px" }}>
                {formik.errors.firstname}
              </p>
            ) : null}
            {/* end of name */}

            {/* email */}
            <TextField
              id="email"
              {...formik.getFieldProps("email")}
              sx={{ marginTop: "15px" }}
              label="Email"
              placeholder="Enter email"
              fullWidth
            />
            {formik.touched.email && formik.errors.email ? (
              <p style={{ color: "red", fontSize: "12px" }}>
                {formik.errors.email}
              </p>
            ) : null}
            {/* end of email */}

            {/* number */}
            <TextField
              {...formik.getFieldProps("number")}
              id="number"
              label="Number"
              type="number"
              fullWidth
              placeholder="Enter number"
              sx={{ marginTop: "15px" }}
            />
            {formik.touched.number && formik.errors.number ? (
              <p style={{ color: "red", fontSize: "12px" }}>
                {formik.errors.number}
              </p>
            ) : null}
            {/* end of number */}

            {/* password  */}
            <FormControl
              sx={{ marginTop: "15px" }}
              fullWidth
              variant="outlined"
            >
              <InputLabel htmlFor="outlined-adornment-password">
                Password
              </InputLabel>
              <OutlinedInput
                {...formik.getFieldProps("password")}
                id="password"
                placeholder="Enter password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
                label="Password"
              />
              {formik.touched.password && formik.errors.password ? (
                <p style={{ color: "red", fontSize: "12px" }}>
                  {formik.errors.password}
                </p>
              ) : null}
            </FormControl>
            {/* end of password */}
            {/* confirm password */}
            <FormControl
              sx={{ marginTop: "15px" }}
              fullWidth
              variant="outlined"
            >
              <InputLabel htmlFor="outlined-adornment-password">
                Password
              </InputLabel>
              <OutlinedInput
                {...formik.getFieldProps("confirmpassword")}
                id="confirmpassword"
                placeholder="Confirm password"
                type={showPassword ? "text" : "password"}
                endAdornment={
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword}
                      onMouseDown={handleMouseDownPassword}
                      edge="end"
                    >
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                }
                label="ConfirmPassword"
              />
              {formik.touched.confirmpassword &&
              formik.errors.confirmpassword ? (
                <p style={{ color: "red", fontSize: "12px" }}>
                  {formik.errors.confirmpassword}
                </p>
              ) : null}
            </FormControl>
            {/* end of confirm password */}

            {/* checkbox & button */}
            <FormControlLabel
              defaultChecked
              label="I accept the rules"
              control={<Checkbox name="checked" color="primary" />}
            />
            <Button
              style={btnstyle}
              variant="contained"
              fullWidth
              type="submit"
              sx={{
                color: "white",
                bgcolor: "#13AE8C",
                "&:hover": {
                  background: "#13AE8C",
                },
              }}
            >
              Sign UP
            </Button>
            <Typography sx={{ marginTop: "10px" }}>
              I alredy have account
              <Link style={linkstyle} to="/Login" href="#">
                Log in
              </Link>
            </Typography>
            {/* end of checkbox & button */}
            <Grid marginTop="25%" align="center">
              <Typography color="teal" fontSize="small">
                Developed by Minoo Zarpoosh
              </Typography>
            </Grid>
          </Paper>
        </Paper>
      </form>
    </Grid>
  );
};

export default SignUp;
